<template>
  <div class="details">
     <b-container class="bv-example-row">
      <b-row class="mt-3">
        <b-col cols="18" md="12" class="mt-5">
          <div>
            <b-card>
              <b-media no-body>
                <b-media-body class="ml-3" v-show="flag" v-if="details != null ? flag=true : falg=false">
                  <span @click="toPrePage" class="toPre">返回</span>
                  <div class="cnName">
                    <h4 class="mt-3 detailsName">{{ details.cnName }}</h4>
                  </div>
                  <div class="details_container mt-1">
                      <h5 class="details_span">类型</h5>
                     <p>{{ type }}</p>
                  </div>
                  <div class="details_container">
                      <h5 class="details_span">别名</h5>
                     <p>{{ details.aliasesName }}</p>
                  </div>
                  <div class="details_container">
                      <h5 class="details_span">生产厂商</h5>
                     <p>{{ details.company }}</p>
                  </div>
                  <div class="details_container">
                      <h5 class="details_span">性能</h5>
                     <p>{{ details.performance }}</p>
                  </div>
                 <div class="details_container">
                    <h5 class="details_span">用途</h5>
                    <p>{{ details.purpose }}</p>
                 </div>
                 <div class="details_container">
                   <h5 class="details_span">组成结构</h5>
                   <p>{{ details.structure }}</p>
                 </div>
                 <div class="details_container">
                   <h5 class="details_span">简要制法</h5>
                    <p>{{ details.method }}</p>
                 </div>
                 <div class="details_container mb-0">
                   <h5 class="details_span">染整工艺</h5>
                   <p>{{ details.technology}}</p>
                 </div>
                </b-media-body>
              </b-media>
            </b-card>
          </div>
        </b-col>
      </b-row>
      
      <b-row class="mt-5" v-show="isVisable">
        <b-col cols="18" md="12">
          <div class="text-center">
            <b-spinner variant="dark" style="width: 3rem; height: 3rem;" type="grow" label="Text Centered"></b-spinner>
          </div>
        </b-col>
      </b-row>


    </b-container>   
  </div>
</template>

<script>

export default {
  name: 'apitest', //name为文件名的全小写
  data () {
    return{
      isVisable:true,
      d_id: this.$route.params.id,
      type: this.$route.params.type,
      details: null,
      flag: false,
      current_page: 1
    }
  },
  mounted () {
    var api = `https://www.domyown.top/ssm/getDetail/${this.d_id}`
    this.axios
      .get(api)
      .then(response => {
        this.isVisable = false
        this.details = response.data.extend.detailsInfo
      })
      .catch(error => (console.log(error)))
  },
  methods:{
    toPrePage () {
       this.$router.push({ name: 'searchResult', params: {searchContent: this.type }});
    }
  },
  beforeRouteLeave (to,from,next) {
    to.meta.keepAlive = true
    next()
  },
  components: {

  }
}
</script>

<style lang="less" scoped>
.cnName{
  border-bottom: 1px #000 solid;
  padding-bottom: 5px;
}
.detailsName{
  text-align: center;
}
.toPre{
  border: 1px #000 solid;
  padding: 5px;
  border-radius: 2px;
  cursor: pointer;
}

.details_span{
  padding: 5px 0px;
}
</style>
